.input {
	background-color: transparent;
	border: 1px solid transparent;
	border-radius: 3px;
	font-size: 1rem;
	font-family: inherit;
	transition: border 0.2s ease;
	outline: none;
	padding: 3px;
}

.input:hover {
	border: 1px solid rgba(0, 0, 0, 0.15);
}

.input:focus {
	border: 1px solid rgba(0, 0, 0, 0.3);
}
