* {
	margin: 0;
	box-sizing: border-box;
}

html,
body {
	font-family: Cambria;
}

:root {
	--green: rgba(64, 216, 122, 0.2);
}

.container {
	margin: 0 auto;
	padding: 0 15px;
}

.main-title {
	text-align: center;
	margin: 0px 0px 20px 0px;
}

.create-board-form {
	margin: 0px 0px 10px 0px;
	background-color: var(--green);
}

.change-board-title-form {
	display: none !important;
	width: 0;
	height: 0;
}

.change-board-title-form.active {
	width: max-content;
	height: auto;
	display: flex !important;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 1;
	background-color: rgb(231, 176, 176);
}

.board {
	border: 1px solid rgba(0, 0, 0, 0.15);
	padding: 5px;
	border-radius: 7px;
	background-color: rgba(231, 176, 176, 0.15);
	margin: 0px 0px 15px 0px;
	position: relative;
}

.board-title {
	font-weight: 700;
	font-size: 1.5rem;
	display: inline-block;
	margin: 0px 0px 0px 10px;
}

.change-board-title-button {
	vertical-align: bottom !important;
	justify-self: start;
}

.board-top {
	padding: 5px 0;
	border-bottom: 1px solid rgba(0, 0, 0, 0.15);
	margin: 0px 0px 15px 0px;
	display: grid;
	gap: 15px;
	grid-template-rows: auto;
	grid-template-columns: max-content auto auto;
	align-items: center;
}

.board-delete-button {
	justify-self: end;
	font-weight: 600;
	font-size: 1.2rem;
}

.board-content {
	display: flex;
	gap: 20px;
	align-items: flex-start;
	/* overflow-x: auto;
	overflow-y: visible; */
	overflow: auto;
}

.create-group-form {
	background-color: var(--green);
}

.group {
	background-color: rgba(64, 216, 122, 0.2);
	padding: 7px;
	border-radius: 7px;
	width: 300px;
	display: flex;
	flex-direction: column;
	height: max-content;
	margin: 0px 0px 10px 0px;
}

.group-top {
	margin: 0px 0px 5px 0px;
	padding: 0px 0px 5px 0px;
	border-bottom: 1px solid rgba(0, 0, 0, 0.15);
}

.group-title {
	display: inline-block;
	width: 90%;
	font-weight: 500;
	font-size: 1.1rem;
}

.edit-group-tittle {
	vertical-align: middle;
}

.group-content {
	min-height: 15px;
}

.task {
	padding: 5px;
	background-color: #fff;
	border-radius: 5px;
	border: 1px solid rgba(0, 0, 0, 0.1);
	position: relative;
}

.task:hover .task-button {
	visibility: visible;
	opacity: 1;
}

.task:not(:last-child) {
	margin: 0px 0px 5px 0px;
}

.task[data-status='true'] {
	text-decoration: line-through;
}

.task-button {
	position: absolute;
	top: 2px;
	right: 2px;
	z-index: 2;
	visibility: hidden;
	opacity: 0;
	transition: all 0.2s ease;
	background-color: rgba(0, 0, 0, 0.05);
}

.task-menu {
	position: absolute;
	top: 2px;
	right: 0;
	z-index: 5;
	background-color: #ffffff;
	border-radius: 5px;
	overflow: hidden;
	list-style: none;
	padding: 0px 5px;
	box-shadow: 0 0 7px rgba(0, 0, 0, 0.15);
	width: max-content;
	max-height: 0;
	transition: max-height 0.2s ease 0s;
}

.task-button:hover + .task-menu,
.task-menu:hover {
	max-height: 100px;
	padding: 5px;
}

.task-menu.open {
	opacity: 1;
	visibility: visible;
	height: max-content;
}

.task-menu-item {
	margin: 0px 0px 5px 0px;
}

.task-content.done {
	text-decoration: line-through;
}

.add-new-task-form {
	margin: 15px 0px 0px 0px !important;
	border-top: 1px solid rgba(0, 0, 0, 0.15);
	padding: 5px 0px 0px 0px !important;
	border-radius: 0 !important;
	display: flex;
	column-gap: 5px;
}

.new-task {
	flex: 1 1 auto;
}

.my-input {
	background-color: transparent;
	border: 1px solid transparent;
	border-radius: 3px;
	font-size: 1rem;
	font-family: inherit;
	transition: border 0.2s ease;
	outline: none;
	padding: 3px;
}

.my-input:hover {
	border: 1px solid rgba(0, 0, 0, 0.15);
}
